.container-fluid{
    margin-top: 20px;
  }

jumbotron, h1, p{
    padding: 10px
}

.carousel-inner{
    width:100%;
    max-height: 400px !important;
  }

#footer{
    border:1px solid white;
    margin-top: 10px;
    padding:10px;
}


.card{
    border:1px solid black;
    padding:10px;
    margin :30px !important;
}
#basic-nav-dropdown{
    color:white;
}

#topslip{
    border:1px solid black;
    margin:10px;
    padding: 5px;
}

.accordion img{
    width:50%;
    padding:5px;
}

@media(min-width:1367px) and (max-width:1920px){
    #tiles{
        width:450px
    }
    #paradise{
        width:750px;
        height:380px;
        padding:10px;
    }
    .jumbo {
        height:600px;
        width:1250px;
    }
    #jumb{
        margin-left: 100px;
      }
}

@media (min-width:768px) and (max-width:1366px){
    #tiles{
        width:450px
    }
    #paradise{
        width:750px;
        height:380px;
        padding:10px;
    }
    .jumbo {
        height:600px;
        width:1250px;
    }
    #jumb{
        margin-left: 20px;
      }

}
@media(max-width:767px){
    #tiles{
        width:450px;
    }
    #paradise{
        width:400px;
        height:300px;
        padding:5px;
    }
    .jumbo{
        height:300px;
    }
}



